<template>
<div>
    <div v-if="isLoading" class="center pt-5">
        <div class="spinner-border text-primary" role="status">
            <span class="sr-only">Cargando contenido...</span>
        </div>
    </div>
   <DeptoBase v-else
    :goal="cortemaq.meta_semana"
    :progress="cortemaq.progress"
    :name="cortemaq.nombre"
    :depto="cortemaq.depto"
    :days="cortemaq.dias_laborados"
    :m3="cortemaq.m3_persona"
    :bonus_p="cortemaq.bono_persona"
    :bonus_tot="cortemaq.bono_depto"
    :asis="cortemaq.asistencia"
    :extra="cortemaq.$_extra_m3"
    :bonus_prod="cortemaq.bono_productividad"
    :bonus_goals="cortemaq.bono_metas"
    />
</div>
</template>


<script>
import DeptoBase from '@/components/DeptoBase';

export default {
    name: 'Cortemaq',
    components: {DeptoBase},
    created() {
        this.$store.dispatch("plants/getInfo", {
            department: 'cortemaq',
            userCode: this.$route.params.user,
            city: 'tijuana'
        }); 
    },
    computed: {
        isLoading() {
            return this.$store.getters["plants/loading"];
        },
        cortemaq() {
        return this.$store.getters["plants/data"];
        }
    }
}
</script>